'use client'

import { useDevices } from '@/hooks'
import { DeviceStatusCard } from '@/components/dashboard/device-status-card'
import { TemperatureChart } from '@/components/dashboard/temperature-chart'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { RefreshCw, Plus, Activity } from 'lucide-react'
import { useState } from 'react'

export default function DashboardPage() {
  const { devices, isLoading, refresh } = useDevices()
  const [refreshing, setRefreshing] = useState(false)

  const handleRefresh = async () => {
    setRefreshing(true)
    await refresh()
    setTimeout(() => setRefreshing(false), 1000)
  }

  // Filter device yang aktif
  const activeDevices = devices.filter(d => d.status === 'online')
  const totalDevices = devices.length

  return (
    <div className="space-y-6">
      {/* Page Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Dashboard</h1>
          <p className="text-gray-500">
            Monitor all your AC devices in real-time
          </p>
        </div>
        <div className="flex gap-3">
          <Button 
            onClick={handleRefresh} 
            variant="outline"
            isLoading={refreshing}
          >
            <RefreshCw className={`mr-2 h-4 w-4 ${refreshing ? 'animate-spin' : ''}`} />
            Refresh
          </Button>
          <Button>
            <Plus className="mr-2 h-4 w-4" />
            Add Device
          </Button>
        </div>
      </div>

      {/* Stats Overview */}
      <div className="grid gap-6 md:grid-cols-3">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">
              Total Devices
            </CardTitle>
            <Activity className="h-4 w-4 text-gray-400" />
          </CardHeader>
          <CardContent>
            <div className="text-3xl font-bold">{totalDevices}</div>
            <p className="text-xs text-gray-500 mt-1">
              {activeDevices.length} active, {totalDevices - activeDevices.length} offline
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">
              Online Rate
            </CardTitle>
            <div className="h-3 w-3 rounded-full bg-green-500 animate-pulse" />
          </CardHeader>
          <CardContent>
            <div className="text-3xl font-bold text-green-600">
              {totalDevices > 0 
                ? Math.round((activeDevices.length / totalDevices) * 100)
                : 0}%
            </div>
            <p className="text-xs text-gray-500 mt-1">
              System uptime: 99.8%
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">
              Energy Saved Today
            </CardTitle>
            <span className="text-2xl">âš¡</span>
          </CardHeader>
          <CardContent>
            <div className="text-3xl font-bold text-blue-600">
              12.4 kWh
            </div>
            <p className="text-xs text-gray-500 mt-1">
              ~Rp 18,600 saved
            </p>
          </CardContent>
        </Card>
      </div>

      {/* Loading State */}
      {isLoading && (
        <div className="flex items-center justify-center h-64">
          <div className="text-center">
            <div className="h-12 w-12 animate-spin rounded-full border-4 border-blue-500 border-t-transparent mx-auto mb-4" />
            <p className="text-gray-500">Loading devices...</p>
          </div>
        </div>
      )}

      {/* No Devices State */}
      {!isLoading && devices.length === 0 && (
        <Card className="flex items-center justify-center h-64">
          <CardContent className="text-center">
            <Activity className="h-12 w-12 text-gray-300 mx-auto mb-4" />
            <h3 className="text-lg font-semibold text-gray-700 mb-2">
              No Devices Found
            </h3>
            <p className="text-gray-500 mb-4">
              Get started by adding your first AC device
            </p>
            <Button>
              <Plus className="mr-2 h-4 w-4" />
              Add Device
            </Button>
          </CardContent>
        </Card>
      )}

      {/* Devices Grid */}
      {!isLoading && devices.length > 0 && (
        <>
          <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
            {devices.map((device) => (
              <DeviceStatusCard
                key={device.device_id}
                deviceId={device.device_id}
                deviceName={device.name}
              />
            ))}
          </div>

          {/* Temperature Charts */}
          <div className="grid gap-6 lg:grid-cols-2">
            {devices.slice(0, 2).map((device) => (
              <TemperatureChart
                key={device.device_id}
                deviceId={device.device_id}
              />
            ))}
          </div>
        </>
      )}
    </div>
  )
}